// Styleguide 6.1
@import (reference) '../common/colors';
@import (reference) '../common/font';
@import (reference) '../lib/_mixins';

form {
  .coveo-table-form {
    display: table;
    width: 100%;

    text-align: left;

    table-layout: fixed;

    transition: opacity 0.35s ease;

    .form-group {
      display: table-row;
      height: 36px;

      color: @block-text-color;

      &.hidden {
        display: none;
      }

      &.disabled .value {
        background-color: transparent;
      }

      & > label:first-child, .value {
        display: table-cell;

        vertical-align: middle;

        border-top: 2px solid white;
        border-bottom: solid 1px @table-header-background-color-dark;
      }

      & > label:first-child {
        width: 30%;
        padding: 0 10px;

        color: @table-header-text-color;
        font-family: @base-font-family;
        font-size: 16px;

        background-color: @body-background-color;
      }

      .context-help {
        float: right;
      }

      .coveo-checkbox + span, .coveo-checkbox + button {
        margin-left: 10px;
      }

      .value {
        height: 39px;

        font-size: 16px;

        background-color: @table-editable-cell-color;

        input:disabled {
          color: black;
          font-family: @base-font-family;
          font-size: 16px;
          font-weight: 600;

          background-color: white;
        }

        &.disabled {
          background-color: white;
        }

        input, textarea, .admin-select {
          box-sizing: border-box;
          padding: 6px 10px;
        }

        input {
          width: 100%;
          height: 39px;

          background-color: transparent;
          box-shadow: none;
          border-width: 0;

          resize: none;
        }

        input[type='file'] {
          padding: 9px 10px;
        }

        &.coveo-slide-toggle-label {
          padding: 0 10px;
        }

        &.admin-editable:hover {
          background-color: @table-editable-cell-hover;
        }

        &.admin-non-editable {
          padding-left: 9px;

          background-color: @body-background-color;

          i {
            margin-right: 5px;

            vertical-align: text-bottom;
          }
        }

        .admin-multiline {
          margin-bottom: 0;

          border: none;
        }

        .admin-select {
          width: 100%;

          .dropdown-toggle {
            box-sizing: border-box;
            width: 100%;
            padding-right: 0;
            padding-left: 0;

            font-size: 16px;
            text-align: left;

            background-color: transparent;
            border: none;

            cursor: pointer;
            opacity: 1;

            boxe-shadow: none;;

            .coveo-sprites-misc-dropdown_arrow {
              position: absolute;
              right: 0;
            }
          }

          .dropdown-menu {
            box-sizing: border-box;
            width: 100%;
            max-height: 250px;
            overflow-y: auto;

            text-align: left;

            .slim-scroll();;

            li a i {
              margin-top: 0;
              margin-bottom: 0;

              vertical-align: text-top;
            }
          }
        }

        textarea {
          width: 100%;
          margin-bottom: 0;

          background-color: transparent;
          box-shadow: none;
          border: none;

          resize: vertical;

          .slim-scroll();
        }
      }

      &.input-validation-error, &.multiline-input-validation-error {
        .input-validation-error-details {
          display: block;
          padding: 0 10px;

          color: white;
          font-size: 11px;
          line-height: 15px;

          background-color: @invalid-message-background-color;

          i {
            vertical-align: -2px;
          }
        }

        label {
          color: @error-color;
        }

        label, .value {
          border-bottom: solid 1px @invalid-message-background-color;
        }
      }

      &.input-validation-error {
        .value {
          background-color: @invalid-background-color;
        }
      }

      &.multiline-input-validation-error .erroneous-input {
        background-color: @invalid-background-color;
      }
    }

    &.disabled {
      opacity: 0.4;

      .form-group label {
        cursor: default;
      }
    }

    & + .coveo-table-form, & + .coveo-block-form {
      margin-top: 10px;
    }
  }
}
