// Coveo Flex Form
//
// Simple form using flex display. Used for boxed coveo-slide-toggle-label.
//
// Markup:
// <form>
//   <div class="coveo-flex-form">
//     <label class="coveo-slide-toggle-label boxed">
//       <span class="toggle-label">Simple text</span>
//       <input type="checkbox" class="coveo-slide-toggle">
//       <button></button>
//     </label>
//     <label class="coveo-slide-toggle-label boxed">
//       <span class="toggle-label">Simple text</span>
//       <input type="checkbox" class="coveo-slide-toggle">
//       <button></button>
//       <span class="toggle-description">My very long description explaining what is my checkbox doing in order to help the user...</span>
//     </label>
//   </div>
// </form>
//
// Styleguide 6.3

form .coveo-flex-form {
  flex-flow: row wrap;

  display: flex;
  margin: -10px 0 0 -10px;

  .coveo-slide-toggle-label.boxed {
    flex: auto;

    width: 0; // Fix to make all boxes the same size
    margin: 10px 0 0 10px;
  }
}
