.collapsable-panel-group .collapse {
  display: none;
  visibility: hidden;
}
.collapsable-panel-group .collapse.in {
  display: table;
  width: 100%;
  visibility: visible;
}
tr.collapsable-panel-group .collapse.in {
  display: table-row;
}
tbody.collapsable-panel-group .collapse.in {
  display: table-row-group;
}
.collapsable-panel-group .collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition-duration: 0.35s;
  transition-property: height, visibility;
  transition-timing-function: ease;
}
.collapsable-panel-group .panel {
  border-bottom: 2px solid #dddddd;
}
.collapsable-panel-group .panel + .panel {
  margin-top: 15px;
}
.collapsable-panel-group .panel .panel-heading {
  position: relative;
}
.collapsable-panel-group .panel .panel-heading a {
  display: block;
  padding: 3px 0 3px 26px;
  margin-bottom: 5px;
  color: #666666;
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
  font-size: 15px;
  line-height: initial;
  text-decoration: none;
  text-transform: uppercase;
}
.collapsable-panel-group .panel .panel-heading i.toggle-icon {
  position: absolute;
  top: 8px;
  left: 10px;
  z-index: -1;
  content: '\00a0';
  background-image: url("../images/CoveoStyleGuide.Sprites.png");
  display: inline-block;
  background-repeat: no-repeat;
  overflow: hidden;
  background-size: 83px 16px;
  background-position: -73px 0px;
  width: 10px;
  height: 10px;
  text-indent: 10px;
}
.collapsable-panel-group .panel .panel-heading a.collapsed + i.toggle-icon {
  background-image: url("../images/CoveoStyleGuide.Sprites.png");
  display: inline-block;
  background-repeat: no-repeat;
  overflow: hidden;
  background-size: 83px 16px;
  background-position: -63px 0px;
  width: 10px;
  height: 10px;
  text-indent: 10px;
}
.collapsable-panel-group .panel.panel-default .panel-body {
  padding: 0 10px 10px 10px;
}
